'use client';

import React, { useState, useRef, useEffect } from 'react';
import emailjs from '@emailjs/browser';
import toast, { Toaster } from 'react-hot-toast';
import { motion } from 'framer-motion';

// Initialize EmailJS with your public key
emailjs.init(process.env.NEXT_PUBLIC_EMAILJS_PUBLIC_KEY);

const emojis = ['😊', '😍', '🤔', '😢', '😡'];

export default function ContactUs() {
  // Contact Form State
  const [contactForm, setContactForm] = useState({
    fullName: '',
    email: '',
    subject: '',
    message: '',
  });

  // Feedback Form State
  const [feedbackForm, setFeedbackForm] = useState({
    username: '',
    rating: 3,
    message: '',
  });

  const [loadingContact, setLoadingContact] = useState(false);
  const [loadingFeedback, setLoadingFeedback] = useState(false);
  const contactFormRef = useRef(null);
  const feedbackFormRef = useRef(null);

  // Handle Contact Form Input
  const handleContactChange = (e) => {
    const { name, value } = e.target;
    setContactForm((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  // Handle Feedback Form Input
  const handleFeedbackChange = (e) => {
    const { name, value } = e.target;
    setFeedbackForm((prev) => ({
      ...prev,
      [name]: name === 'rating' ? parseInt(value) : value,
    }));
  };

  // Validate Contact Form
  const validateContactForm = () => {
    if (!contactForm.fullName.trim()) {
      toast.error('Please enter your full name');
      return false;
    }
    if (!contactForm.email.trim()) {
      toast.error('Please enter your email');
      return false;
    }
    if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(contactForm.email)) {
      toast.error('Please enter a valid email');
      return false;
    }
    if (!contactForm.subject.trim()) {
      toast.error('Please enter a subject');
      return false;
    }
    if (!contactForm.message.trim()) {
      toast.error('Please enter your message');
      return false;
    }
    return true;
  };

  // Validate Feedback Form
  const validateFeedbackForm = () => {
    if (!feedbackForm.username.trim()) {
      toast.error('Please enter your username');
      return false;
    }
    return true;
  };

  // Send Contact Email
  const handleContactSubmit = async (e) => {
    e.preventDefault();

    if (!validateContactForm()) return;

    setLoadingContact(true);

    try {
      const templateParams = {
        to_email: 'roadmapfinder25@gmail.com',
        from_name: contactForm.fullName,
        from_email: contactForm.email,
        subject: contactForm.subject,
        message: contactForm.message,
        type: 'contact',
      };

      await emailjs.send(
        process.env.NEXT_PUBLIC_EMAILJS_SERVICE_ID,
        process.env.NEXT_PUBLIC_EMAILJS_CONTACT_TEMPLATE_ID,
        templateParams
      );

      toast.success("Message sent successfully! Well get back to you soon.")
      setContactForm({
        fullName: '',
        email: '',
        subject: '',
        message: '',
      });
    } catch (error) {
      console.error('Error sending contact email:', error);
      toast.error('Failed to send message. Please try again.');
    } finally {
      setLoadingContact(false);
    }
  };

  // Send Feedback Email
  const handleFeedbackSubmit = async (e) => {
    e.preventDefault();

    if (!validateFeedbackForm()) return;

    setLoadingFeedback(true);

    try {
      const templateParams = {
        to_email: 'roadmapfinder25@gmail.com',
        username: feedbackForm.username,
        rating: feedbackForm.rating,
        emoji: emojis[feedbackForm.rating - 1],
        message: feedbackForm.message || 'No additional message',
        type: 'feedback',
      };

      await emailjs.send(
        process.env.NEXT_PUBLIC_EMAILJS_SERVICE_ID,
        process.env.NEXT_PUBLIC_EMAILJS_FEEDBACK_TEMPLATE_ID,
        templateParams
      );

      toast.success('Thank you for your feedback! We appreciate it.');
      setFeedbackForm({
        username: '',
        rating: 3,
        message: '',
      });
    } catch (error) {
      console.error('Error sending feedback email:', error);
      toast.error('Failed to send feedback. Please try again.');
    } finally {
      setLoadingFeedback(false);
    }
  };

  // Animation variants
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.2,
        delayChildren: 0.1,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: {
        duration: 0.6,
        ease: 'easeOut',
      },
    },
  };

  const formVariants = {
    hidden: { opacity: 0, x: -20 },
    visible: {
      opacity: 1,
      x: 0,
      transition: {
        duration: 0.8,
        ease: 'easeOut',
      },
    },
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-purple-900 to-slate-900 py-12 px-4 sm:px-6 lg:px-8 relative overflow-hidden">
      <Toaster position="top-right" />

      {/* Animated background elements */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <div className="absolute -top-40 -right-40 w-80 h-80 bg-purple-500 rounded-full mix-blend-multiply filter blur-3xl opacity-20 animate-blob"></div>
        <div className="absolute -bottom-40 -left-40 w-80 h-80 bg-blue-500 rounded-full mix-blend-multiply filter blur-3xl opacity-20 animate-blob animation-delay-2000"></div>
        <div className="absolute top-1/2 left-1/2 w-80 h-80 bg-pink-500 rounded-full mix-blend-multiply filter blur-3xl opacity-20 animate-blob animation-delay-4000"></div>
      </div>

      {/* Main Content */}
      <div className="relative z-10 max-w-7xl mx-auto">
        {/* Header Section */}
        <motion.div
          className="text-center mb-16"
          variants={containerVariants}
          initial="hidden"
          animate="visible"
        >
          <motion.h1
            className="text-4xl sm:text-5xl lg:text-6xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-white via-purple-200 to-purple-400 mb-4 leading-tight"
            variants={itemVariants}
          >
            Get in Touch with RoadmapFinder
          </motion.h1>
          <motion.p
            className="text-lg sm:text-xl text-gray-300 max-w-2xl mx-auto"
            variants={itemVariants}
          >
            Have questions or want to share your experience? We'd love to hear from you.
          </motion.p>
        </motion.div>

        {/* Forms Container */}
        <motion.div
          className="grid grid-cols-1 lg:grid-cols-2 gap-8"
          variants={containerVariants}
          initial="hidden"
          animate="visible"
        >
          {/* Contact Form Card */}
          <motion.div
            className="backdrop-blur-md bg-white/10 border border-white/20 rounded-3xl p-8 shadow-2xl hover:shadow-3xl transition-all duration-300"
            variants={formVariants}
          >
            <h2 className="text-3xl font-bold text-white mb-2">Contact Us</h2>
            <p className="text-gray-300 mb-8">
              Got a question? Drop us a line and we'll respond as soon as possible.
            </p>

            <form ref={contactFormRef} onSubmit={handleContactSubmit} className="space-y-6">
              {/* Full Name */}
              <div>
                <label className="block text-sm font-semibold text-white mb-2">
                  Full Name <span className="text-red-400">*</span>
                </label>
                <input
                  type="text"
                  name="fullName"
                  value={contactForm.fullName}
                  onChange={handleContactChange}
                  placeholder="John Doe"
                  className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500 focus:border-transparent transition-all duration-200"
                />
              </div>

              {/* Email */}
              <div>
                <label className="block text-sm font-semibold text-white mb-2">
                  Email Address <span className="text-red-400">*</span>
                </label>
                <input
                  type="email"
                  name="email"
                  value={contactForm.email}
                  onChange={handleContactChange}
                  placeholder="john@example.com"
                  className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500 focus:border-transparent transition-all duration-200"
                />
              </div>

              {/* Subject */}
              <div>
                <label className="block text-sm font-semibold text-white mb-2">
                  Subject <span className="text-red-400">*</span>
                </label>
                <input
                  type="text"
                  name="subject"
                  value={contactForm.subject}
                  onChange={handleContactChange}
                  placeholder="How can we help?"
                  className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500 focus:border-transparent transition-all duration-200"
                />
              </div>

              {/* Message */}
              <div>
                <label className="block text-sm font-semibold text-white mb-2">
                  Message <span className="text-red-400">*</span>
                </label>
                <textarea
                  name="message"
                  value={contactForm.message}
                  onChange={handleContactChange}
                  placeholder="Your message here..."
                  rows="5"
                  className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500 focus:border-transparent transition-all duration-200 resize-none"
                ></textarea>
              </div>

              {/* Submit Button */}
              <motion.button
                type="submit"
                disabled={loadingContact}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                className="w-full py-3 px-6 rounded-lg bg-gradient-to-r from-purple-500 to-pink-500 text-white font-semibold hover:shadow-lg transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {loadingContact ? 'Sending...' : 'Send Message'}
              </motion.button>
            </form>
          </motion.div>

          {/* Feedback Form Card */}
          <motion.div
            className="backdrop-blur-md bg-white/10 border border-white/20 rounded-3xl p-8 shadow-2xl hover:shadow-3xl transition-all duration-300"
            variants={formVariants}
          >
            <h2 className="text-3xl font-bold text-white mb-2">Share Your Feedback</h2>
            <p className="text-gray-300 mb-8">
              Help us improve! Tell us about your experience with RoadmapFinder.
            </p>

            <form ref={feedbackFormRef} onSubmit={handleFeedbackSubmit} className="space-y-6">
              {/* Username */}
              <div>
                <label className="block text-sm font-semibold text-white mb-2">
                  Username <span className="text-red-400">*</span>
                </label>
                <input
                  type="text"
                  name="username"
                  value={feedbackForm.username}
                  onChange={handleFeedbackChange}
                  placeholder="Your username"
                  className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-pink-500 focus:border-transparent transition-all duration-200"
                />
              </div>

              {/* Rating */}
              <div>
                <label className="block text-sm font-semibold text-white mb-4">
                  How would you rate your experience?
                </label>
                <div className="space-y-4">
                  <input
                    type="range"
                    name="rating"
                    min="1"
                    max="5"
                    value={feedbackForm.rating}
                    onChange={handleFeedbackChange}
                    className="w-full h-2 bg-gray-600 rounded-lg appearance-none cursor-pointer accent-pink-500"
                  />
                  <div className="flex justify-between items-center">
                    <div className="flex gap-2">
                      {emojis.map((emoji, idx) => (
                        <motion.button
                          key={idx}
                          type="button"
                          onClick={() =>
                            setFeedbackForm((prev) => ({
                              ...prev,
                              rating: idx + 1,
                            }))
                          }
                          whileHover={{ scale: 1.2 }}
                          whileTap={{ scale: 0.9 }}
                          className={`text-3xl transition-all duration-200 ${
                            feedbackForm.rating === idx + 1
                              ? 'scale-125 drop-shadow-lg'
                              : 'opacity-50 hover:opacity-75'
                          }`}
                        >
                          {emoji}
                        </motion.button>
                      ))}
                    </div>
                    <span className="text-white font-semibold">
                      {feedbackForm.rating}/5
                    </span>
                  </div>
                </div>
              </div>

              {/* Optional Message */}
              <div>
                <label className="block text-sm font-semibold text-white mb-2">
                  Additional Feedback (Optional)
                </label>
                <textarea
                  name="message"
                  value={feedbackForm.message}
                  onChange={handleFeedbackChange}
                  placeholder="Tell us what we can improve..."
                  rows="4"
                  className="w-full px-4 py-3 rounded-lg bg-white/5 border border-white/10 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-pink-500 focus:border-transparent transition-all duration-200 resize-none"
                ></textarea>
              </div>

              {/* Submit Button */}
              <motion.button
                type="submit"
                disabled={loadingFeedback}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                className="w-full py-3 px-6 rounded-lg bg-gradient-to-r from-pink-500 to-purple-500 text-white font-semibold hover:shadow-lg transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {loadingFeedback ? 'Sending...' : 'Submit Feedback'}
              </motion.button>
            </form>
          </motion.div>
        </motion.div>
      </div>

      {/* CSS for animations */}
      <style jsx>{`
        @keyframes blob {
          0%, 100% {
            transform: translate(0, 0) scale(1);
          }
          33% {
            transform: translate(30px, -50px) scale(1.1);
          }
          66% {
            transform: translate(-20px, 20px) scale(0.9);
          }
        }

        .animate-blob {
          animation: blob 7s infinite;
        }

        .animation-delay-2000 {
          animation-delay: 2s;
        }

        .animation-delay-4000 {
          animation-delay: 4s;
        }
      `}</style>
    </div>
  );
}